<template>
  <div id="index" class="flex-center flex-column" style="height:100%;" @click="_test_audio">
    <div style="font-size: 48px;margin-bottom: 110px;">鲜美佳朝阳店</div>
    <div style="height:130px;margin-bottom: 40px;">
      <img src="@/assets/img/hook.png"/>
    </div>
    <div style="font-size: 24px;margin-bottom: 8px;">收款金额</div>
    <div style="font-size: 36px;font-weight: 700;margin-bottom:60px;">{{amountOfMoney}}元</div>
    <div style="font-size: 14px;color:#999;">——<span style="margin: 0 16px;">{{dateTimeStr}}</span>——</div>

    <div style="display:none;">
      <!-- audio area -->
      <audio class="audio-0" src="@/assets/audio/0.mp3"></audio>
      <audio class="audio-1" src="@/assets/audio/1.mp3"></audio>
      <audio class="audio-2" src="@/assets/audio/2.mp3"></audio>
      <audio class="audio-3" src="@/assets/audio/3.mp3"></audio>
      <audio class="audio-4" src="@/assets/audio/4.mp3"></audio>
      <audio class="audio-5" src="@/assets/audio/5.mp3"></audio>
      <audio class="audio-6" src="@/assets/audio/6.mp3"></audio>
      <audio class="audio-7" src="@/assets/audio/7.mp3"></audio>
      <audio class="audio-8" src="@/assets/audio/8.mp3"></audio>
      <audio class="audio-9" src="@/assets/audio/9.mp3"></audio>
      <audio class="audio-10" src="@/assets/audio/10.mp3"></audio>
      <audio class="audio-hundred" src="@/assets/audio/hundred.mp3"></audio>
      <audio class="audio-thousand" src="@/assets/audio/thousand.mp3"></audio>
      <audio class="audio-yuan" src="@/assets/audio/yuan.mp3"></audio>
      <audio class="audio-get" src="@/assets/audio/get.mp3"></audio>
      <audio class="audio-point" src="@/assets/audio/point.mp3"></audio>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      audios: {},
      amountOfMoney: '0.00',
      dateTimer: null,
      dateTimeStr: '----------- --:--',
      playStatus: false,
      msgList: []
    }
  },
  mounted(){
    this.initDateTimer();
    this.initAudios();
    this.initWheel();
  },
  destroyed(){
    clearInterval(this.dateTimer);
  },
  methods: {
			
			
    /**
     * 初始化日期时间动画
     */
    initDateTimer(){
      let that = this;
      this.dateTimer = setTimeout(function(){
        requestAnimationFrame(function(){
          let date = new Date();
          that.dateTimeStr = 
            date.getFullYear() + '-' +
            ('0' + (date.getMonth()+1)).substr(-2) + '-' + 
            ('0' + date.getDate()).substr(-2) + ' ' +
            ('0' + date.getHours()).substr(-2) + ':' +
            ('0' + date.getMinutes()).substr(-2);
          that.initDateTimer();
        })
      },1000)
    },
        
        
    /**
    * 初始化音频
    * @return {Promise} 该promise.then表示所有音频文件都加载完毕并且可以播放
    */
    initAudios(){
      let audios = {
        '0': document.querySelector('.audio-0'),
        '1': document.querySelector('.audio-1'),
        '2': document.querySelector('.audio-2'),
        '3': document.querySelector('.audio-3'),
        '4': document.querySelector('.audio-4'),
        '5': document.querySelector('.audio-5'),
        '6': document.querySelector('.audio-6'),
        '7': document.querySelector('.audio-7'),
        '8': document.querySelector('.audio-8'),
        '9': document.querySelector('.audio-9'),
        '10': document.querySelector('.audio-10'),
        'hundred': document.querySelector('.audio-hundred'),
        'thousand': document.querySelector('.audio-thousand'),
        'yuan': document.querySelector('.audio-yuan'),
        'get': document.querySelector('.audio-get'),
        'point': document.querySelector('.audio-point')
      };
      this.audios = audios;
    },
    
    
    /**
     * 初始化轮询
     */
    initWheel(){
      // this.updateAmountOfMoney();
    },
    
    
    /**
     * 读一个音频
     */
    read(str,callback){
      let audios = this.audios;
      let audio = audios[str];
      if(!audio) callback && callback();
      for(let k in audios) audios[k].pause();
      audio.play();
      audio.addEventListener('ended',()=>{
        callback && callback()
      });
    },
        
        
    /**
    * 递归播报金额部分（例1111.11）
    */
    playMoney(callback, amountOfMoney = '0.00', charIndex = 0){
      let amountOfMoneyLength = amountOfMoney.length;
      if(charIndex > amountOfMoneyLength - 1){ // 跳出递归
        return callback && callback();
      }
    
      let c = amountOfMoney[charIndex] || '0'; // currentChar
      let pC = amountOfMoney[charIndex-1] || '0'; // prevChar
      let ppC = amountOfMoney[charIndex-2] || '0';
      let pppC = amountOfMoney[charIndex-3] || '0';
      let nC = amountOfMoney[charIndex+1] || '0'; // nextChar
      let nnC = amountOfMoney[charIndex+2] || '0';
      let nnnC = amountOfMoney[charIndex+3] || '0';
    
      let r = this.read;
      let z = char => char === '0'; // isZero
      let t = () => this.playMoney(callback, amountOfMoney, ++charIndex); // toNext
      
      /*
        下面这段逻辑写的很乱，解释下
        该逻辑是判断收款金额当前位(charIndex)的字符应该怎么读，比如1010.01的第1位应该读 一千
        
        金额的每一位的读法有3钟可能
        1，不读。比如1000.00元，除了第1位读 一千，其他位都不读。读作 一千元
        2，读零。比如1001.00元，第2位和第3位合读 零。读作一千零一元
        3，读一~九并读单位。比如1111.00元，第1位读一并读千，第2位读一并读百。读作一千一百一十一元
        
        变量 c 是收款金额当前位(charIndex)上的 字符
        函数 r(str,callback) 负责读一个与str对应的音频，一个音频读完毕之后调用回调函数callback
        函数 t() 负责进入下一个递归，并让金额的位索引charIndex加1
        函数 z(char) 判断char是否等于'0',是返回true
        
        r(c,t) 表示读完毕c对应的音频，然后进入下一递归
        t() 表示直接进入下一递归
      */
      
      if(charIndex === amountOfMoneyLength - 7){ // 千位
          if(z(c)){
            if(z(pC) || (z(nC) && z(nnC) && z(nnnC))){ // 不读
                t();
            }else{ // 读零
                r(c,t)
            }
          }else{ // 读千位
            r(c,()=>{
                r('thousand',t)
            })
          }
      }else if(charIndex === amountOfMoneyLength - 6){ // 百位
          if(z(c)){
            if(z(pC) || (z(nC) && z(nnC))){ // 不读
                t();
            }else{ // 读零
                r(c,t);
            }
          }else{ // 读百位
            r(c,()=>{
                r('hundred',t)
            })
          }
      }else if(charIndex === amountOfMoneyLength - 5){ // 十位
          if(z(c)){
            if(z(pC) || z(nC)){ // 不读
                t();
            }else{ // 读零
                r(c,t)
            }
          }else{ // 读十位
            r(c,()=>{
                r('10',t)
            })
          }
      }else if(charIndex === amountOfMoneyLength - 4){ // 个位
          if(z(c)){
            if(!z(pC) || !z(ppC) || !z(pppC)){ // 不读
                t();
            }
          }else{ // 读个位
            r(c,t);
          }
      }else if(charIndex === amountOfMoneyLength - 3){
          if(z(nC) && z(nnC)){
            t();
          }else{
            r('point',t);
          }
      }else if(charIndex === amountOfMoneyLength - 2){
          if(z(c) && z(nC)){
            t();
          }else{
            r(c,t);
          }
      }else if(charIndex === amountOfMoneyLength - 1){
          if(z(c)){
            t();
          }else{
            r(c,t);
          }	
      }
    },
    
    
    /**
     * 更新收款金额（并且语音播报)
     * @param {Number} amountOfMoney 收款金额
     * @return {Promise}
     */
    updateAmountOfMoney(callback){
      if(this.playStatus) return;
      this.playStatus = true;
      
      let amountOfMoney = parseFloat(this.msgList.shift()).toFixed(2);
      
      this.amountOfMoney = amountOfMoney;
      
      this.read('get',()=>{
          this.playMoney(()=>{
            this.read('yuan',()=>{
                this.playStatus = false;
                if(this.msgList.length > 0){
                  this.updateAmountOfMoney(callback);
                }else{
                  callback && callback();
                }
            })
          },amountOfMoney)
      })
    },
		
      
    _test_audio(){
      this.msgList.push(1111.11);
      this.updateAmountOfMoney();
    }

  }
}
</script>

